import React from 'react';

import Button from '@material-ui/core/Button';
import './index.scss';

const ZPButton = ({ className, titleClassName, ...props }) => (
  <Button
    variant="contained"
    color="secondary"
    size="small"
    {...props}
    className={`zp-button ${className}`}
  >
    <span className={`title ${titleClassName}`}>{props.title}</span>
  </Button>
);
export default ZPButton;
