import './toast.scss';
import React from 'react';
import PropTypes from 'prop-types';

const TOAST_TIME = 3000;
const FADE_OUT_TIME = 500;
const FADE_IN_TIME = 500;

export default class Toast extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      isVisible: true
    };
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({ isVisible: false }, () => {
        setTimeout(this.props.requestClose, FADE_OUT_TIME);
      });
    }, this.props.duration + FADE_IN_TIME);
  }
  render() {
    const className = this.state.isVisible ? 'toast' : 'toast none';
    return (
      <div className={className}>
        <div className="toast__toast-content">
          <span className="icon-general_check" />
          <span className="toast__toast-content__toast-msg">
            {this.props.message}
          </span>
        </div>
      </div>
    );
  }
}

Toast.propTypes = {
  message: PropTypes.string,
  duration: PropTypes.number,
  requestClose: PropTypes.func.isRequired
};
Toast.defaultProps = {
  message: '',
  duration: TOAST_TIME,
  requestClose: () => {}
};
