import React from 'react';
import './index.scss';

import Button from '@material-ui/core/Button';
import { ZPWrapperPageHOC } from '../../components';

const INTRODUCE_TITLE = 'Săn nuôi heo lớn nhận\nquà tặng lớn';
const INTRODUCE_BUTTON_TITLE = 'Săn Heo Ngay';
const GUIDE_TEXT = 'Hướng dẫn săn heo';
const GIFT_TITLE = 'Nhiều phần quà giá trị';
const GIFT_DESCRIPTION =
  'Tổng giá trị lên tới <b>1.000.000đ</b> khi nuôi\nheo lớn lên tới Cấp 5.';

const guideAR = {
  title: 'Cách săn Heo bằng AR',
  contents: [
    {
      src: require('./images/guide-ar-1@3x.png'),
      description: '<b>1.</b> Vào tính năng quét AR'
    },
    {
      src: require('./images/guide-ar-2@3x.png'),
      description:
        '<b>2.</b> Hướng camera vào hình ảnh event được quy định để tìm Heo'
    },
    {
      src: require('./images/guide-ar-3@3x.png'),
      description: '<b>3.</b> Chọn vào để bắt Heo về nuôi lớn'
    }
  ]
};

const guideQR = {
  title: 'Cách săn Heo bằng QR Code',
  contents: [
    {
      src: require('./images/guide-ar-1@3x.png'),
      description: '<b>1.</b> Vào tính năng quét AR'
    },
    {
      src: require('./images/guide-ar-2@3x.png'),
      description:
        '<b>2.</b> Hướng camera vào hình ảnh event được quy định để tìm Heo'
    },
    {
      src: require('./images/guide-ar-3@3x.png'),
      description: '<b>3.</b> Chọn vào để bắt Heo về nuôi lớn'
    }
  ]
};

const GuideRow = ({ src, description }) => (
  <div className="row">
    <img className="image" src={src} />
    <div
      className="description"
      dangerouslySetInnerHTML={{
        __html: description
      }}
    />
  </div>
);

class GuideBox extends React.PureComponent {
  render() {
    const { title, contents } = this.props;
    return (
      <div className="guide">
        <span className="title">{title}</span>
        {contents.map((e, index) => (
          <GuideRow key={index} {...e} />
        ))}
        <Button className="main-button" variant="contained">
          <span className="title">{INTRODUCE_BUTTON_TITLE}</span>
        </Button>
      </div>
    );
  }
}

class Introduce extends React.PureComponent {
  handleHunting = () => {
    window.ZaloPay.scanARCampaign((resp) => {
      alert('scan result: ' + JSON.stringify(resp));
    });
  };
  render() {
    return (
      <div className="introduce">
        <div className="header-background" />
        <div className="header">
          <img
            className="pig-image"
            src={require('./images/pig-introduce@3x.png')}
          />
          <span className="title">{INTRODUCE_TITLE}</span>
          <Button
            className="main-button main-button-round"
            variant="contained"
            onClick={this.handleHunting}
          >
            <span className="title">{INTRODUCE_BUTTON_TITLE}</span>
          </Button>
          <div className="guide-button">
            <span className="title">{GUIDE_TEXT}</span>
            <img className="down-image" src={require('./images/down@3x.png')} />
          </div>
          <div className="gift-wapper">
            <span className="title">{GIFT_TITLE}</span>
            <div
              className="description"
              dangerouslySetInnerHTML={{
                __html: GIFT_DESCRIPTION
              }}
            />
          </div>
          <img
            className="red-packet"
            src={require('./images/redenvelop-list@3x.png')}
          />
        </div>
        <div className="background-footer">
          <GuideBox {...guideAR} />
          <GuideBox {...guideQR} />
        </div>
      </div>
    );
  }
}

export default ZPWrapperPageHOC(Introduce);
